<template>
  <v-container fluid class="mt-3">
    <v-row>
      <v-col cols="12">
        <v-card>
          <v-card-text>
            <v-data-table
              :items="USER_LIST"
              :headers="headers"
              :search="search"
              sort-by="created_at"
              :sort-desc="true"
              :loading="isLoading"
              loading-text="Loading... Please wait"
              v-model="selected"
              show-select
              item-key="_id"
            >
              <template v-slot:top>
                <v-toolbar flat>
                  <v-btn
                    color="primary"
                    class="mr-2"
                    @click="$router.push('/user/add')"
                  >
                    
                  </v-btn>

                  <v-btn color="error" @click="delAll">  </v-btn>

                  <v-spacer></v-spacer>
                  <!--  -->
                  <v-text-field
                    v-model="search"
                    append-icon="mdi-magnify"
                    label="Search"
                    single-line
                    hide-details
                  ></v-text-field>

                  <!--  -->
                  <v-dialog v-model="dialogDelete" max-width="500px">
                    <v-card>
                      <v-card-title class="headline"
                        >?</v-card-title
                      >
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="closeDelete"
                          ></v-btn
                        >
                        <v-btn
                          color="blue darken-1"
                          text
                          @click="deleteItemConfirm"
                          ></v-btn
                        >
                        <v-spacer></v-spacer>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </v-toolbar>
              </template>

              <template v-slot:[`item.avatar_url`]="{ item }">
                <v-avatar size="36px">
                  <img
                    :src="item.avatar_url || require('@/assets/user.png')"
                    :alt="item.name"
                  />
                </v-avatar>
              </template>

              <template v-slot:[`item.articles`]="{ item }">
                {{ item.articles.length }}
              </template>

              <template v-slot:[`item.type`]="{ item }">
                <span v-if="item.type === 'SuperAdmin'"></span>
                <span v-else-if="item.type === 'Admin'"></span>
                <span v-else></span>
              </template>

              <template v-slot:[`item.actions`]="{ item }">
                <v-icon
                  class="mr-2"
                  color="primary"
                  @click="$router.push(`/user/edit/${item._id}`)"
                >
                  mdi-pencil
                </v-icon>
                <v-icon color="error" @click="del(item)"> mdi-delete </v-icon>
              </template>

              <template v-slot:no-data>  </template>

              <template v-slot:no-results>  </template>
            </v-data-table>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { DELETE_USER, FETCH_USER_LIST } from "@/store/actionTypes";
import { formatDate } from "@/libs/utils";

export default {
  name: "UserList",
  data: () => ({
    isLoading: false,
    selected: [],
    isDelAll: false,
    search: "",
    dialogDelete: false,
    currentEditIndex: "",
    headers: [
      {
        text: "",
        value: "avatar_url",
        align: "center",
        sortable: false,
      },
      {
        text: "",
        value: "nickName",
        align: "center",
        sortable: false,
      },
      {
        text: "",
        value: "articles",
        align: "center",
        sortable: true,
      },
      {
        text: "",
        value: "following_count",
        align: "center",
        sortable: true,
      },
      {
        text: "",
        value: "followers_count",
        align: "center",
        sortable: true,
      },
      {
        text: "",
        value: "type",
        align: "center",
        sortable: false,
      },
      {
        text: "",
        value: "created_at",
        align: "center",
        sortable: true,
      },
      {
        text: "",
        value: "actions",
        align: "center",
        sortable: false,
      },
    ],
  }),

  computed: {
    ...mapState(["userList"]),
    USER_LIST() {
      return formatDate(this.userList);
    },
  },

  watch: {
    dialogDelete(val) {
      val || this.closeDelete();
    },
    userList(val) {
      if (val) {
        this.isLoading = false;
      }
    },
  },

  methods: {
    ...mapActions([FETCH_USER_LIST]),

    del(item) {
      this.dialogDelete = true;
      this.currentEditIndex = item._id;
    },

    delAll() {
      if (this.selected.length) {
        this.dialogDelete = true;
        this.isDelAll = true;
      } else {
        this.$message.error("");
      }
    },

    async deleteItemConfirm() {
      this.isLoading = true;
      try {
        if (this.isDelAll) {
          for (const user of this.selected) {
            await this.$store.dispatch(DELETE_USER, user._id);
          }
        } else {
          await this.$store.dispatch(DELETE_USER, this.currentEditIndex);
        }
        this.$message.success("");
      } catch (error) {
        this.isLoading = false;
      }
      this.closeDelete();
    },

    closeDelete() {
      this.dialogDelete = false;
      this.currentEditIndex = "";
      this.isDelAll = false;
      this.isLoading = false;
    },
  },
  async created() {
    this.isLoading = true;
    await this.FETCH_USER_LIST({
      page: 1,
      per_page: 300,
    });
    this.isLoading = false;
  },
};
</script>
